
h1{
	text-align: left;
	font-family: "Lucida Sans";
	font-size: 24px;
}
h2{
	font-family: "Lucida Sans";
	font-size: 14px;
}
html, body{
	height: 100%;
	margin: 0;
}

/* #image{
	min-width: 400px;
	position: relative;
	//left: 100px;
	//top: 150px;
}*/
#main{
	display: grid;
	margin: 0;
	/*justify-content: center;*/
	height: 100%;
	width: 100%;
	grid-template-columns: 100% ;
	/* grid-template-rows: 111px auto; */
	grid-template-rows: 100vh auto;
	grid-gap: 0px;
	grid-template-areas:
		'header'
		'content';
}
	#header{
		background: #1F3B59;
		grid-area: header;
		display: flex;
		flex-direction: column;
		/* display: grid; */
		/* grid-template-areas: "caixa_com_nome" "contatos"; */
		align-items: center;
		justify-content: center;
		max-width: 100%;
		margin: 0;
		padding: 0px 10px;
	}
			#caixa_com_nome{
				grid-area: caixa_com_nome;
				/* background: green; */
				width: 518px;
				height: 55px;
				border-style: solid;
				border-color: black;
				margin: 0;
				flex-shrink: 1;
			}
				#title{
					text-align: center;
					font-family: "Caviar Dreams";
					font-weight: bold;
					color: white;
					font-size: 22px;
				}
			#contatos{
				display: flex;
				flex-direction: row;
				/* margin: 0 0 0 auto; */
				grid-area: contatos;
			}
				.contato{
					/*background-color: green;*/
					margin: 5px;
					/*width: 250px;*/
					overflow: auto;
					display: flex;
					justify-content: center;
				}
				.icone{
					float: left;
					width: 20px;
					vertical-align: middle;
					margin: auto 10px;
				}
				.contato_texto{
					font-family: "Montserrat";	
					font-size: 13px;	
					color: white;	
				}


	#content{
		grid-area: content;
		/* background-color:red; */
		display: grid;
		grid-template-areas: 
			"Perfil"
			"Texto_botoes";
		justify-content: center;
		align-items: center;
		padding-top: 20px;
		/* max-width: 97%; */
		width: 100%;
		/*min-height: 100px;*/
		top: 0px;
		margin: 0 auto; 
	}
		#Perfil{
			display: grid;
			/* background-color: blue; */
			grid-area: Perfil;
			grid-template-areas: "texto_inicial minha_foto";
			justify-content: center;
			align-items: center;
			width: 100vw;
			/* max-width: 100%; */
			min-height: 750px;
		}
			#textoInicial{
				display: flex;
				grid-area: texto_inicial;
				flex-direction: column;
				justify-content: center;
				/*background-color: rgba(152, 179, 209, 0.17);*/
				/* background-color: white; */
				height: 750px;
				width: 33vw;
				margin: 0 10px;
			}
			.titulo{
				margin: 10px 85px;
			}
			.texto{
				margin: 10px 85px;
			}
			#minhaFoto{
				grid-area: minha_foto;
				max-height: 750px;
				/* object-fit: cover; */
				margin: 0 auto 0 auto;
				width: auto;
				justify-self: center;
			}
		#texto_botoes{
			width: 100vw;
			/* background-color: green; */
			background-color: rgba(152, 179, 209, 0.17);
			/* display: flex; */
			/* flex-direction: row; */
			display: grid;
			grid-area: Texto_botoes;
			grid-template-areas: "caixa texto_dinamico";
			align-items: center;
			justify-content: center;
			/* margin-top: 20px; */
			margin: 20px auto 0;
			/* max-width: 95%; */
			padding: 150px 0;
		}
			#botoes{
				/*background-color: red;*/
				grid-area: caixa;
				display: flex;
				flex-direction: column;
				align-items: center;
				margin: 0 10px;
				width: 562.5px;
			}
					.caixa_com_titulo{
						display: flex;
						/* background-color: red; */
					background-color: rgba(31, 59, 89, 0.5);
					align-items: center;
					font-size: 18px;
					padding: auto auto auto auto;
					justify-content: center;
					margin: 0 10px 30px 0;
						width: 250px;
					border-radius: 5px;
					}
					.caixa_com_titulo:hover {
						background-color: grey;
					}
			#texto_dinamico{
				display: flex;
				/*background-color: rgba(152, 179, 209, 0.17);*/
				/* background-color: white; */
				flex-direction: column;
				justify-content: center;
				grid-area: texto_dinamico;
				height: 200px;
				width: 33vw;
				max-width: 573;
				margin-left:10px;
			}
				/* .conteudo{

				} */

	/*.footer{
		position: fixed;
		width: 100%;
		min-height: 200px;
		bottom:   10xp;
		background: lightpink;
	}*/
	.row {
		display: flex;
		flex-wrap: wrap;
		padding: 0 4px;
		justify-content: center;
	}

	/* Create four equal columns that sits next to each other */


	#image {
	display: block;
		margin-top: 8px;
		/* vertical-align: middle; */
		max-width: 100%;
	}

	/* Responsive layout - makes a two column-layout instead of four columns */
	@media (max-width: 518px) {
		#caixa_com_nome{
			width: 100%;
		}
	}
	@media (max-width: 563px) {
		#minhaFoto{
			object-fit: cover;
			width: 95vw;
			height: auto;
			margin: 0 auto;
		}
		#textoInicial{
			width: 10vw;
		}
		#botoes{
			width: 95vw;
		}
		#Perfil{
			display: grid;
			/* background-color: red; */
			grid-template-areas: 
			"minha_foto" 
			"texto_inicial";
			width: 98%;
			height: 200vh;
		}	

		#texto_botoes{
			display: grid;
			/* background-color: red; */
			grid-template-areas: 
			"caixa" 
			"texto_dinamico";
			width: 100%;
			justify-content: center;
			align-items: center;
			/* height: 200vh; */
			/* padding-bottom: 200px; */
			
		}
		#textoInicial{
			width: 562.5px;
			/* max-width: 562px; */
			margin: 0 auto;
			justify-self: center;
		}
		#texto_dinamico{
			width: 562.5px;
			max-width: 100vw;
		}
		#botoes{
			/* background-color: red; */
			/* margin: 200px auto; */
			height: 100px;
			max-width: 95;
		}
		#contatos{
			flex-direction: column;
		}
	}
	@media (min-width: 936px){
		#textoDinamico{
			max-width: 100%;
		}
	}

	/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
	@media screen and (max-width: 600px) {
		.column {
		flex: 100%;
		max-width: 100%;
		}
	}

